a.nav-link {
	font-size: 1.2rem;
	color: #262262;
	transition: 0.3s ease-in-out;
	font-weight: bold;
	margin-left: 2rem;
	&:hover {
		color: #262262;
		transition: 0.3s ease-in-out;
		text-decoration: underline;
	}
}
.navbar-nav > .active > a { 
    color: #262262;
    text-decoration: underline;
    font-weight: bold;
}
.navbar-brand {
	margin-left: 1rem;
}
.btn-gold {
    color: #222;
    background-color: #eac959;
    border-color: #bf9a1c;
    transition: 0.3s ease-in-out;
    &:hover {
    	color: #222;
    	background-color: #ffe281;
    	border-color: #bf9a1c;
    	transition: 0.3s ease-in-out;
    }
}